import React from 'react';
import { HashLink as Link } from 'react-router-hash-link';
import PropTypes from 'prop-types';

class Event extends React.Component {


  render() {
    // console.log("Event");
    return (
      <div className='element events'>
        <h4>{this.props.month}</h4>
        <ul>
          {this.props.eventlist.map((item) => {
            return(
              <li key={item.eid}>
                <p>{item.name}</p>
                {(item.link !== '') && (
                  <p className='ev-link'>
                    <Link className='ev-link-txt' to={item.link}>
                    過去のイベントの様子はこちら
                      <i className="fas fa-external-link-alt onepoint-color"></i>
                    </Link>
                  </p>
                )}
              </li>
              );
            })}
        </ul>
      </div>
    );
  }
}

Event.propTypes = {
  month: PropTypes.string,
  eventlist: PropTypes.array
};

export default Event;
